/* @flow */

import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router } from 'react-router-dom';
import App from 'containers/App/App';

ReactDOM.render(
  <Router>
    <App />
  </Router>,
  document.querySelector('#app'),
);



